.titleBar {
    margin: 0 20px;
}

.topPanel p {
    color: #333;
    margin: 0 20px 25px;
    align-self: flex-end;
}

.topPanel>div {
    display: flex;
    flex-wrap: wrap;
}

.leftRight {
    display: flex;
}

.leftPanel {
    padding: 20px;
}

.rightPanel {
    background: #eee;
    margin: 20px;
    width:100%;
}

.flexMenu .flexTable .propertyName {
    margin-right: 20px;
    margin-bottom: 5px;
}

.flexMenu .flexTable .propertyValue {
    margin-right: 10px;
}

.flexMenu .flexTable .propertyValue:not(.active) a {
    text-decoration: none;
}

.flexMenu .label {
    white-space: nowrap;
}

.flexMenu .exportAsImage input {
    font-weight: 600;
    text-transform: capitalize;
    width: 100%;
    padding: 5px;
    margin: 15px 0;
}

.flexTable {
    border-spacing: 6px;
}

.flexTable td {
    vertical-align: baseline;
}

.imageList {
    list-style-type: none;
    padding: 0;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}

.imageList img {
    width: 56px;
    margin: 5px;
}

body>.img {
    list-style-type: none;
}

body>.img>img {
    max-width: 50%;
    border: 1px dashed black;
}

.logogrid {
    padding: 20px;
}

.logogrid .canvas > div {
    box-sizing: border-box;
}

.logogrid .canvas img {
    width: 100%;
    display: block;
}

.canvasWrapper {
    text-align: center;
    display: table;
    border-spacing: 10px;
}

.canvasWrapper.width .size {
    border-bottom: 1px solid black;
}

.canvasWrapper.height {
    border-right: 1px solid black;
    vertical-align: middle;
}

.canvasWrapper.height .size {
    transform: rotate(-90deg);
    margin-right: -5px
}

.exportedImageContainer img {
    max-width: 650px;
    height: auto;
    border: 1px dashed black;
}